<template>
    <div class="Linkman">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
      <div class="fiexoverTitle border_E3E4E8 mt24" @click="Relationshow=true">
          <div class="fiexover">
              <p>安全设置</p>
              <div>
                <span>{{securitySettings}}</span>
                <i class="iconfont"></i>
              </div>
          </div>
      </div>
      <div class="fiexoverTitle mt24" v-if="OtherContacts==true" @click="showPicker=true">
        <div class="fiexover border_E3E4E8">
          <p>历史订单联系人</p>
          <div class="f34 cl_333" style="height: 100%; line-height: 1.02rem;">
<!--            <input type="text" style="border: none; text-align: right; line-height: .5rem" >-->
            <i class="iconfont"></i>
          </div>
        </div>
      </div>
      <div class="fiexoverTitle"  @click="OtherContacts==true?unify(1):''">
        <div class="fiexover border_E3E4E8">
          <p>
            <i style="color: red; vertical-align: middle; display: inline-block;">*</i>姓名
          </p>
          <div class="f34 cl_333" style="height: 100%; line-height: 1.02rem;">
<!--            <input type="text" style="border: none; text-align: right; line-height: .5rem" v-if="OtherContacts==true">-->
            <span>{{compellation}}</span>
            <i class="iconfont" v-if="OtherContacts==true"></i>
          </div>
        </div>
      </div>

      <div class="fiexoverTitle" @click="OtherContacts==true?unify(2):''">
        <div class="fiexover border_E3E4E8">
          <p>
            <i style="color: red; vertical-align: middle; display: inline-block;">*</i>手机号
          </p>
          <div class="f34 cl_333" style="height: 100%; line-height: 1.02rem;" >
<!--            <input type="text" style="border: none; text-align: right;line-height: .5rem"  v-if="OtherContacts==true">-->
            <span>{{phoneNumber}}</span>
            <i class="iconfont" v-if="OtherContacts==true"></i>
          </div>
        </div>
      </div>
      <div class="fiexoverTitle" @click="OtherContacts==true?unify(3):''">
        <div class="fiexover border_E3E4E8">
          <p>
            <i style="color: red; vertical-align: middle; display: inline-block;">*</i>QQ
          </p>
          <div class="f34 cl_333" style="height: 100%; line-height: 1.02rem;">
            <input type="text" style="border: none; text-align: right;line-height: .5rem" v-model="QQNumber">
            <i class="iconfont"></i>
          </div>
        </div>
      </div>
      <div class="public-btn mt60  mr30 ml30" @click="jump">
        确认
      </div>
      <van-action-sheet v-model="Relationshow" :actions="RelationActions" @select="RelationOnSelect" />
      <van-dialog v-model="importShow" show-cancel-button confirmButtonColor="#f90" confirmButtonText="添加" @confirm="importClick">
        <div class="importJump">
          <div v-if="importIndex==1">
            <p>姓名填写</p>
            <input type="text" placeholder="郭靖" v-model="importModel1">
          </div>
          <div v-if="importIndex==2">
            <p>手机号填写</p>
            <input type="text" placeholder="请输入手机号" v-model="importModel2">
          </div>
          <div v-if="importIndex==3">
            <p>QQ填写</p>
            <input type="text" placeholder="请输入QQ号" v-model="importModel3">
          </div>
        </div>
      </van-dialog>
      <van-popup v-model="showPicker" round position="bottom">
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
    </div>
</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../../common/HomePageTop";

    export default {
        name: "Linkman",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle: { name: "选择订单联系人",left: true},
              //姓名变量
              compellation:"郭靖",
              //手机号变量
              phoneNumber:"17637408019",
              //qq变量
              QQNumber:"1666972845",
              Relationshow: false,
              securitySettings:"当前登录账号",
              RelationActions: [{ name: '当前登录账号' }, { name: '与收件人相同' }, { name: '其他联系人' }],
              OtherContacts:false,
              //姓名填写的弹出窗
              importShow:false,
              //历史订单联系人的弹窗
              showPicker: false,
              //根据下标判断点击的是姓名还是手机号还是qq
              importIndex:"",
              //弹出层输入框
              importModel1:"",
              importModel2:"",
              importModel3:"",
              columns: [
                {text:'杭州1',phone:"17637408019",qq:"1666972845"},
                {text:'杭州2',phone:"17637408019",qq:"1666972845"},
                {text:'杭州3',phone:"17637408019",qq:"1666972845"},
                {text:'杭州4',phone:"17637408019",qq:"1666972845"},
                {text:'杭州5',phone:"17637408019",qq:"1666972845"},],
            }
        },
        methods: {
          //在弹出框中点击添加
          importClick(){
            switch (this.importIndex) {
              case 1:
                this.compellation=this.importModel1;
                break;
              case 2:
                this.phoneNumber=this.importModel2;
                break;
              case 3:
                this.QQNumber=this.importModel3;
                break;
            }


          },



          onConfirm(value) {
            this.compellation=value.text;
            this.phoneNumber=value.phone;
            this.QQNumber=value.qq;

            this.showPicker = false;
          },
          //判断弹出层弹的是哪个
          unify(i){
            this.importShow=true;
           this.importIndex=i;
          },
          RelationOnSelect(item) {
            // 默认情况下点击选项时不会自动收起
            // 可以通过 close-on-click-action 属性开启自动收起
            this.Relationshow = false;
            this.securitySettings=item.name
            if(item.name=="其他联系人"){
              this.OtherContacts=true
            }else{
              this.OtherContacts=false;
            }
          },
        //  跳转回去
          jump(){
            this.$router.push({
              name:"YesOrder",
              params:{name:this.securitySettings},
            })

          },
        },
        created() {


        }
    }

</script>

<style scoped>
  input{width: 3.72rem;}
  .van-dialog {padding-top: 0.48rem;border-radius: inherit;font-size: 0.38rem;}
  .importJump{text-align: center;padding: 0 .3rem }
  .importJump p{font-size: .34rem;color: #333;}
  .importJump input{border: 1px solid #D1D3D8;display: block;width: 100%;margin-top: .3rem;margin-bottom: .3rem;height: .8rem;padding-left: .2rem;font-size: .28rem}
</style>



